    padding: 88px 30px !important;
  }
  .col-card {
    margin-left: 68px !important;
    margin-right: 140px !important;
  }
}

@media screen and (max-width: 768px) {
  .col-card {
    margin-left: 50px !important;
    margin-right: 235px !important;
  }
}

.col-card {
  float: left;
  width: 25%;
  margin-left: 40px;
  margin-right: 40px;
}

.card {
  width: 275px;
  height: 394px;
  padding: 20px 16px 10px 16px;
  background-color: white;
  border-radius: 5px;
}

.card-image {
  width: 100%;
  height: 280px;
  object-fit: cover;
}

.product-content {
  display: flex;
  justify-content: space-between;
}

.product-h3 {
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #333333;
  margin-top: 25px;
  margin-bottom: 0;
}

.product-h2 {
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 26px;
  color: #333333;
  margin-top: -5 !important;
}

.content2-button {
  width: 46px;
  height: 46px;
  border: none;
  outline: none;
  border-radius: 10%;
  background-color: #4847bc !important;
  margin-top: 65%;
}

.content2-img {
  width: 18px;
  height: 18px;
  vertical-align: middle;
}

/* css hor homecontent */

.prod-head {
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 34px;
  color: #828282;
  margin-left: 1%;
}

.btn {
  width: 200px;
  height: 40px;
  background: #ffffff !important;
  border: 1px solid #bdbdbd !important;
  box-sizing: border-box !important;
  border-radius: 4px !important;
  box-shadow: none !important;
  float: right;
  margin-right: 3%;
}

.mycont {
  padding: 0 70px;
}
.dropdown-toggle::after {
  margin-left: 6.255em !important;
}

.dropdown-menu.show {
  display: block;
  width: 200px;
}

/* accordion css */
.accordiana {
  margin-bottom: 67px;
}

.accordion-button:focus {
  box-shadow: none !important;
  border-color: lightgrey !important;
}

.accordion-button:not(.collapsed) {
  color: black !important;
  background-color: white !important;
}

.accordion-button {
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 34px;
  color: #333333;
  border: none !important;
}

.accordion-button:not(.collapsed)::after {
  transform: rotate(180deg) !important;
}

.accordion-collapse {
  border: none !important;
  border-width: none !important;
}
.accordion-body {
  font-size: 14px;
  padding: 1rem 0px !important;
}

