@media only screen and (max-width: 450px) {
  .card {
    margin-left: -60px !important;
  }
  .tot-rating {
    display: none;
  }
  .card2-text {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    color: #353535;
    margin-top: 320px;
  }
  .price {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 26px;
    color: #333333;
    margin-top: 8px !important;
  }
  .homecard-button {
    width: 46px;
    height: 46px;
    border: none;
    outline: none;
    border-radius: 10%;
    background-color: #5d3a84 !important;
    float: right;
    margin-top: -25%;
  }

  .res {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .filter-start {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: row-reverse;
  }

  .filter-but {
    border: none !important;
    outline: none !important;
    display: inline-block !important;
    width: 125px;
    height: 43px;
    margin-right: 10px;
  }

  .mycont {
    padding: 0 0px;
  }
  .btn {
    width: 125px !important;
    height: 43px;
    margin-right: 0px;
  }
  .prod-head {
    margin-left: 13%;
  }

  .dropdown-toggle::after {
    margin-left: 2em !important;
  }

  .accordiana {
    display: none;
    /* margin-bottom: 67px; */
  }

  .modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    outline: 0;
    animation: animatebottom 0.4s !important;
    margin-bottom: 150px;
    overflow: scroll !important;
    max-height: 455px !important;
  }
  @keyframes animatebottom {
    from {
      bottom: -300px;
      opacity: 0;
    }

    to {
      bottom: 0;
      opacity: 1;
    }
  }
  .dropdown-menu {
    min-width: 7rem !important;
  }
  .dropdown-menu.show {
    width: 126px !important;
  }
  .modal-title {
    line-height: 34px;
    font-size: 14px;
    font-weight: 400;
  }
  .accord-last-line {
    display: none;
  }
  .close-but {
    float: right;
    width: 28px;
    height: 28px;
  }
}

